

export function xt_camera(id) {
    top.postMessage({method:"xt_camera",params:{"xtid":id},},"*");
}

export function xt_getPictures(maximumImagesCount,id) {
    top.postMessage({method:"xt_getPictures",params:{"maximumImagesCount":maximumImagesCount,"xtid":id},},"*");
}

export function xt_fileTransfer(reqUrl, fileURL, id, options){
    top.postMessage({method:"xt_fileTransfer",params:{"reqUrl":reqUrl,"fileURL":fileURL,"xtid":id,"options":options},},"*");
}

export function xt_saveImageDataToLibrary(canvasId,id){

    top.postMessage({method:"xt_saveImageDataToLibrary",params:{"canvasId":canvasId,"xtid":id},},"*");
}
